import ValuesImage1 from "../../../assets/img/home/values-1.png";
import ValuesImage2 from "../../../assets/img/home/values-2.png";
import ValuesImage3 from "../../../assets/img/home/values-3.png";
import { aosDelay } from "../../../resources";

export default function ValuesSection() {
    return <section id="values" className="values">
        <div className="container" data-aos="fade-up">
            <header className="section-header">
                <h2>Our Values</h2>
                <p>We are committed</p>
            </header>

            <div className="row">
                <div
                    className="col-lg-4"
                    data-aos="fade-up"
                    data-aos-delay={aosDelay}
                >
                    <div className="box">
                        <img
                            src={ValuesImage1}
                            className="img-fluid"
                            alt=""
                        />
                        <h3>High Quality</h3>
                        <p>
                            We aim to provide only valuable information
                            and the best opportunities to our users
                            <sup className="TnC">*1</sup>
                        </p>
                    </div>
                </div>

                <div
                    className="col-lg-4 mt-4 mt-lg-0"
                    data-aos="fade-up"
                    data-aos-delay={aosDelay}
                >
                    <div className="box">
                        <img
                            src={ValuesImage2}
                            className="img-fluid"
                            alt=""
                        />
                        <h3>Transparency</h3>
                        <p>
                            Our users can anytime request to see their
                            data stored with us and how we use it
                            <sup className="TnC">*2</sup>
                        </p>
                    </div>
                </div>

                <div
                    className="col-lg-4 mt-4 mt-lg-0"
                    data-aos="fade-up"
                    data-aos-delay={aosDelay}
                >
                    <div className="box">
                        <img
                            src={ValuesImage3}
                            className="img-fluid"
                            alt=""
                        />
                        <h3>No Data Sharing</h3>
                        <p>
                            We never share our users data with any third
                            party and any information we collect is
                            stored securely <sup className="TnC">*3</sup>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </section>
}