import { aosDelay, hrefValue, launchLink, postRequestTo, preventDefault } from "../../../resources";
import React, { useEffect, useState } from "react";

export default function HeroCommunitiesSection() {

    const [state, setState] = useState({
        term: "",
        status: "loading",
        communities: []
    });

    function onSearch(e) {
        const search = e.target.value;
        if (search === "") {
            setState({ ...state, status: "searching" });
            loadCommunities();
            return;
        } else {
            const formData = new FormData();
            formData.append("Keywords", search);
            setState({ ...state, status: "searching", term: search });
            postRequestTo(
                "communities/search",
                formData,
                (response) => {
                    // console.log(response)
                    if (response.includes("search_return_results")) {
                        const results = JSON.parse(response);
                        const tempComnnunities = [];
                        for (let i = 0; i < results.length - 1; i++) {
                            tempComnnunities.push(results[i]);
                        }
                        setState({ ...state, communities: tempComnnunities, status: "searchResults", term: search });
                    }
                    if (response.includes("not") && response.includes("found")) {
                        setState({ ...state, status: "searchResultsNotFound", term: search });
                    }
                },
                (response) => {
                    console.log(response);
                }
            )
        }
    }

    function loadCommunities() {
        const formData = new FormData();
        postRequestTo(
            "communities/load",
            formData,
            (response) => {
                if (response.includes("search_return_results")) {
                    const results = JSON.parse(response);
                    const tempComnnunities = [];
                    for (let i = 0; i < results.length - 1; i++) {
                        tempComnnunities.push(results[i]);
                    }
                    setState({ ...state, search: state.search, communities: tempComnnunities, status: "loadedSuggested" });
                }
            },
            (response) => {
                console.log(response);
            }
        )
    }

    useEffect(() => {
        loadCommunities();
        // eslint-disable-next-line
    }, []);

    return <section id="hero" className="hero d-flex align-items-center">
        <div className="container">
            <div className="row">
                <div
                    className="col-lg-6 d-flex flex-column justify-content-center"
                >
                    <h1 data-aos="fade-up" id="appHeading">
                        Explore Communities
                    </h1>
                    <h2 data-aos="fade-up" data-aos-delay={aosDelay}>
                        Expand your network. Grap the best opportunities
                    </h2>
                    <div data-aos="fade-up" data-aos-delay={aosDelay}>
                        <div className="col-lg-10">
                            <form
                                action=""
                                method="post"
                                className="subscribe-form"
                                onSubmit={preventDefault}
                            >
                                <input
                                    type="text"
                                    name="communityName"
                                    placeholder="Enter community name"
                                    onKeyUp={onSearch}
                                />
                                <input type="submit" value="Search" />
                            </form>
                        </div>
                    </div>
                </div>
                <div
                    className="col-lg-6 hero-img"
                    data-aos="zoom-out"
                    data-aos-delay={aosDelay}
                >
                    <div className="card communityCard">
                        <div className="card-body">
                            {
                                (state.status === "loading" || state.status === "loadedSuggested") ? <h5 className="card-title">Suggested Communities</h5> :
                                    (state.status === "searching") ? <h5 className="card-title">Searching for "{state.term}"</h5> :
                                        (state.status === "searchResults") ? <h5 className="card-title">Search results for "{state.term}" </h5> :
                                            (state.status === "searchResultsNotFound") ? <h5 className="card-title">No communities found that match your search "{state.term}" </h5> :
                                                <span className="hidden"></span>
                            }
                            {
                                (state.status === "loading" || state.status === "searching") ?
                                    <React.Fragment>
                                        <div className="shimmer communities">
                                            <div className="shimmer-background"></div>
                                        </div>
                                        <div className="shimmer communities">
                                            <div className="shimmer-background"></div>
                                        </div>

                                        <div className="shimmer communities">
                                            <div className="shimmer-background"></div>
                                        </div>
                                    </React.Fragment>
                                    :
                                    (state.status === "loadedSuggested" || state.status === "searchResults") ?
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th></th>
                                                    <th className="col name"> Community</th>
                                                    <th className="col loc">Location</th>
                                                    <th className="col loc"><i className="bi bi-people-fill"></i></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    state.communities.map((community) => {
                                                        return <tr key={community.ID}>
                                                            <td ><img className="communityLogo" src="/assets/img/icon/icon.svg" alt="Community Logo" draggable={false} /></td>
                                                            <td>
                                                                {community.Name} <br />
                                                                <div className="communitySocialLinks">
                                                                    <a href={community.WhatsApp} target="_blank" rel="noreferrer">
                                                                        <span className="communityGroupButton">
                                                                            <img className="communityGroupIcon" src="assets/img/social/whatsApp.svg" alt="Social Link" />
                                                                        </span>
                                                                    </a>
                                                                    <a href={hrefValue} target="_blank" rel="noreferrer" onClick={(e) => { e.preventDefault(); launchLink("communities_telegram") }} >
                                                                        <span className="communityGroupButton">
                                                                            <img className="communityGroupIcon" src="assets/img/social/telegram.svg" alt="Social Link" />
                                                                        </span>
                                                                    </a>
                                                                    <a href={hrefValue} target="_blank" rel="noreferrer" onClick={(e) => { e.preventDefault(); launchLink("communities_discord") }}>
                                                                        <span className="communityGroupButton">
                                                                            <img className="communityGroupIcon" src="assets/img/social/discord.svg" alt="Social Link" />
                                                                        </span>
                                                                    </a>
                                                                    {
                                                                        (community.Website !== "" || community.Website.toLowerCase === "null") ?
                                                                            <a href={community.Website} target="_blank" rel="noreferrer">
                                                                                <span className="communityGroupButton">
                                                                                    <img className="communityGroupIcon" src="assets/img/social/website.svg" alt="Social Link" />
                                                                                </span>
                                                                            </a> :
                                                                            <span className="hidden"></span>
                                                                    }
                                                                </div>
                                                            </td>
                                                            <td>{community.Location}</td>
                                                            <td>{community.Size}</td>
                                                        </tr>
                                                    })
                                                }
                                            </tbody>
                                        </table> : <span className="hidden"></span>
                            }

                            {/* <table className="table">
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th className="col name"> Community</th>
                                        <th className="col loc">Location</th>
                                        <th className="col loc"><i className="bi bi-people-fill"></i></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td ><span className="communityLogo"></span></td>
                                        <td>
                                            TechVerse International <br />
                                            <div className="communitySocialLinks">
                                                <span className="communityGroupButton">
                                                    <img className="communityGroupIcon" src="assets/img/social/whatsApp.svg" />
                                                </span>
                                                <span className="communityGroupButton">
                                                    <img className="communityGroupIcon" src="assets/img/social/telegram.svg" />
                                                </span>
                                                <span className="communityGroupButton">
                                                    <img className="communityGroupIcon" src="assets/img/social/discord.svg" />
                                                </span>
                                                <span className="communityGroupButton">
                                                    <img className="communityGroupIcon" src="assets/img/social/website.svg" />
                                                </span>
                                            </div>
                                        </td>
                                        <td>International</td>
                                        <td className="communityType"><i className="bi bi-bank2"></i> <i className="bi bi-people-fill"></i></td>
                                    </tr>

                                </tbody>
                            </table> */}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
}